import send from './http'
export default {
  addOrUpdateEnterprise (data : any) {
    let req = {
      data: data,
      action: 'job.addOrUpdateEnterprise'
    }
    return send.fetch(req, 'post')
  },
  getEnterpriseList (data : any) {
    let req = {
      data: data,
      action: 'job.getEnterpriseList'
    }
    return send.fetch(req, 'post')
  },
  addOrUpdateRecruit (data : any) {
    let req = {
      data: data,
      action: 'job.addOrUpdateRecruit'
    }
    return send.fetch(req, 'post')
  },
  getRecruitList (data : any) {
    let req = {
      data: data,
      action: 'job.getRecruitList'
    }
    return send.fetch(req, 'post')
  },
  getEnterpriseInfo (data : any) {
    let req = {
      data: data,
      action: 'job.getEnterpriseInfo'
    }
    return send.fetch(req, 'post')
  }
}
