import * as types from './mutation-types'
import Vue from 'vue'

export default {
  [types.SET_TOKEN] (state : any, sessionId : any) {
    state.token = sessionId
    window.sessionStorage.setItem('sessionId', sessionId)
  },
  [types.SET_BACK_DATA] (state: any, data: any) {
    state.backData = data;
  }
}
